import React, { Suspense } from 'react';
import './App.css';
import './components/chatbot/chatbot.css';
import { WidgetConfig } from './types';

// Lazy load ChatContainer
const ChatContainer = React.lazy(() => import('./components/chatbot/ChatContainer'));

function App() {
  // ProjectId'yi window.convstateaiConfig'den al
  const getProjectId = () => {
    if (typeof window !== 'undefined' && (window as any).convstateaiConfig) {
      return (window as any).convstateaiConfig.projectId;
    }
    return '1'; // Fallback değer
  };

  const config: WidgetConfig = {
    projectId: getProjectId(),
    colors: {
      primary: '#2563EB',
      secondary: '#1D4ED8',
      background: '#FFFFFF',
      text: '#1F2937',
      accent: '#10B981'
    },
    branding: {
      logo: '/imgs/kadirai.jpeg',
      name: 'ConvState AI',
      welcomeMessage: 'Merhaba! Size nasıl yardımcı olabilirim?'
    },
    features: {
      templates: ['catalog', 'checkout', 'wheel-spin'],
      aiEnabled: true,
      eventTracking: true
    },
    styling: {
      position: 'bottom-right',
      size: 'medium',
      theme: 'light'
    }
  };

  // Event handlers
  const handleEvent = (event: any) => {
    // Handle widget events here
  };

  const handleFeedback = (feedback: any) => {
    // Handle feedback here
  };

  return (
    <Suspense fallback={<div className="loading-spinner">Yükleniyor...</div>}>
      <ChatContainer 
        config={config}
        onEvent={handleEvent}
        onFeedback={handleFeedback}
        onToggleChat={() => {}}
      />
    </Suspense>
  );
}

export default App;
